import { Question } from '@/types';
import DifficultyTag from './DifficultyTag';

const QuestionItem = ({ question }: { question: Question }) => {
  const hasUrl = question.url && question.url.length > 0;
  return (
    <a
      href={hasUrl ? question.url[0] : '#'}
      target="_blank"
      rel="noopener noreferrer"
      className="flex items-center justify-between p-4 bg-muted/40 hover:bg-muted/80 rounded-lg transition-colors duration-200"
    >
      <span className="text-card-foreground text-base md:text-lg">
        {question.title}
      </span>
      <DifficultyTag difficulty={question.difficulty} />
    </a>
  );
};

export default QuestionItem;
